body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f0f0f0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

.todo-container {
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0,0,0, 0.1);
  border-radius: 5px;
  width: 300px;
}

.todo-container h1 {
  text-align: center;
  margin: 0 0 20px 0;
}

#todo-form {
  display: flex;
  margin-bottom: 20px;
}

#todo-form input {
  border: 1px solid #ddd;
  padding: 10px;
  border-radius: 3px;
  width: calc(100% - 100px);
  font-size: 16px;
  outline: none;
}

#todo-form button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 3px;
  cursor: pointer;
  margin-left: 10px;
  font-size: 16px;
}

#todo-form button:hover {
  background-color: #45a049;
}

.todo-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.todo-list li {
  margin-bottom: 10px;
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo-list li.completed {
  text-decoration: line-through;
  color: #888;
}

.todo-list li .delete-btn {
  background-color: #ff3333;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 14px;
}

.todo-list li .delete-btn:hover {
  background-color: #e60000;
}

.filter-todo {
  margin-top: 10px;
}

.filter-todo label {
  margin-right: 10px;
  font-size: 16px;
}

#filter-todo {
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #ddd;
  font-size: 16px;
}

#add-more-button {
  background-color: #ff3333;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}

#add-more-button:hover {
  background-color: #e60000;
}